<template>
  <a-spin :spinning="loading" :tip="$t('task.tip')" size="large">
    <div :bordered="false" v-if="!isEdit">
      <a-card>
        <div class="table-page-search-wrapper" style="margin-bottom: 10px;">
          <a-row>
            <a-col :md="21" :sm="21">
              <a-button-group>
                <btn-add type="bt" :text="$t('vee.add_btn')" @click="onAdd"></btn-add>
              </a-button-group>&nbsp;&nbsp;&nbsp;
              <a-input-search v-model="queryParam.description" :placeholder="$t('vee.placeholder')" style="width: 280px;display:inline-block" @search="onSearch" @keyup.enter.native="onSearch"/>&nbsp;&nbsp;&nbsp;
            </a-col>
            <a-col :md="3" :sm="3">
              <sorter @refresh="onSearch" @show="showSearch"></sorter>
            </a-col>
          </a-row>
        </div>
        <s-table  
          ref="table"
          :bordered="false"
          :autoLoad="true"
          :data="loadData"
          size="small"
          rowKey="code"
        >
          <a-table-column 
            :title="this.$t('task.index1')"
            width="50px"
            align="center"
          >
            <span slot-scope="text">
              <a-icon type="schedule" theme="twoTone" class="icon" style="font-size:25px"/>
            </span>
          </a-table-column>
          <a-table-column
            :title="this.$t('vee.index1')"
            width="150px"
            dataIndex="description"
          ></a-table-column>
          <a-table-column
            :title="this.$t('vee.index2')"
            width="150px"
            dataIndex="rulesCount"
          >
            <span slot-scope="text,record">
              <a-button type="link" @click="showRule(record)">
                {{ record.rulesCount }}
              </a-button>
            </span>
          </a-table-column>
          <a-table-column
            :title="this.$t('vee.index3')"
            width="150px"
            dataIndex="filedsCount"
          >
            <span slot-scope="text,record" >
              <a-button type="link" @click="showField(record)">
                {{ record.fieldsCount }}
              </a-button>
            </span>
          </a-table-column>
          <a-table-column
            :title="this.$t('vee.index4')"
            width="150px"
            dataIndex="coperator"
          ></a-table-column>
          <a-table-column
            :title="this.$t('vee.index5')"
            width="150px"
            dataIndex="actived"
            :filters="filters"
            :filterMultiple="false"
            @FilterDropdownVisibleChange="onChange(2)"
          >
            <span slot-scope="text,record">
              <btn-switch 
                :defaultChecked="record.actived=='1'"
                @change="checked => onSwitch(checked, record)"
              ></btn-switch> 
            </span>
          </a-table-column>
          <a-table-column
            :title="this.$t('task.action')"
            dataIndex="action"
          > 
            <span slot-scope="text,record">
              <btn-detail @click="onDetail(record)" type="a"></btn-detail>
              <a-divider type="vertical"/>
              <btn-edit @click="onUpdate(record)" type="a"></btn-edit>
              <a-divider type="vertical"/>
              <btn-remove @click="onRemove(record)" type="a" v-if="record.status!=2"></btn-remove>
            </span>
          </a-table-column>
        </s-table>
      </a-card>
    </div>
    <show-fields ref="showField" ></show-fields>
    <show-rules ref="showRule" ></show-rules>
    <veeconfig-edit 
      v-if="isEdit"
      @ok="onSubmit" 
      @cancel="onCancel" 
      :params="editParams"
    ></veeconfig-edit>
  </a-spin>
</template>
<script>
  import STable from '@/components/table/'
  import VeeconfigEdit from '@/views/vee/config/VeeconfigEdit'
  import Dict from '@/views/public/Dict'
  import { axios } from '@/utils/request'
  import ShowFields from './ShowFields.vue'
  import ShowRules from './ShowRules.vue'
  export default {
    name: 'Index',
    components: {
      STable,
      VeeconfigEdit,
      Dict,
      ShowFields,
      ShowRules
    },
    data () {
      return { 
        loading: false,
        isEdit: false,
        show: true,
        editParams: {},       
        queryParam: {}, 
        row:{},  
        filters: [{text:this.$t('dct.TASK_ACTIVEDY'),value: '1'},{text:this.$t('dct.TASK_ACTIVEDN'),value: '0'}],
        loadData: parameter => {     
              const that = this
              parameter.actived instanceof Array?that.queryParam.actived = parameter.actived[0]:'0'     
             return axios({
              url: '/vee/config/search-list-config',
              method: 'get',
              params: Object.assign(parameter, this.queryParam)
            }).then(res => { 
              return res.result
            })
        }
      }
    },
    methods: {
      showRule(record){
        this.$refs.showRule.showModal(record.code)
      },
      showField(record){
        this.$refs.showField.showModal(record)
      },
      onSearch() {
        this.$refs.table.refresh()
      },
      showSearch() {
        this.show = !this.show
      },
      onAdd() {
        this.editParams = {opType: '1'}
        this.isEdit = true
      },
      onUpdate (record) {       
        this.editParams = {opType: '2',code: record.code}
        this.isEdit = true  
      },
      onDetail (record) {       
        this.editParams = {opType: '3',code: record.code}
        this.isEdit = true  
      },
      onSwitch(checked, record){
        const that = this
        axios({
          url: '/vee/config/switch',
          method: 'post',
          params: {code: record.code, actived: checked?'1':'0'}
        }).then(()=>{
          that.$refs.table.refresh()
        })
      },
      onRemove (record) {
        var _this = this
        axios({
          method: 'delete',
          url: '/vee/config/delete',
          params: {code: record.code}
        }).then(function () {            
          _this.$refs.table.refresh()
        })
      },
      onChange(){
        //this.filters = this.$refs.deviceStatus.getAll()
      },
      onSubmit(){
        this.isEdit = false
        this.$refs.table.refresh()
      },
      onCancel(){
        this.isEdit=false
      }
    }
  }
</script>
<style lang="scss">
.error {
  color: red;
}
.normal {
  color: #00660f;
}
.ant-card-head-title{
  padding:12px 0
}
</style>