<template>
  <div v-if="visible">
    <a-checkbox-group @change="onChange" style="width:90%" :value="checkedDetail">
      <a-row v-for="(item,index) in data" :key="index" style="border-bottom: 1px dotted #ccc;" :class="{'selected':ruleChecked==item.id}">
        <a-checkbox class="item" :value="item.id" @click.stop="showDetail(item,index)" ref="rule">
        </a-checkbox>
        <span @click.stop="showDetail(item,index)" class="cspan">{{ $t('vee.'+item.id+'_L') }}</span>
      </a-row>
    </a-checkbox-group>
    <a-row :gutter="16" style="margin-top:20px;height:10px;background:#f0f2f5">
    </a-row>
    <a-row :gutter="16" style="background:#1890FF;height:30px;line-height:30px;color:white;margin-top:20px;text-align: center;">
      <a-col :span="2">No</a-col>
      <a-col :span="4">{{ $t('task.params') }}</a-col>
      <a-col :span="4">{{ $t('vee.value') }}</a-col>
    </a-row>
    <div v-for="ruleDetail in data" :key="ruleDetail.id" v-show="ruleChecked==ruleDetail.id" :id="ruleDetail.id+params" style="padding-bottom:40px">
      <a-row :gutter="16" style="height:30px;line-height:30px;text-align: center;margin-top:2px;" v-if="ruleDetail.detail.parameCount>=1">
        <a-col :span="2">1</a-col>
        <a-col :span="4">{{ $t('vee.'+ruleDetail.detail.parame1Lang) }}</a-col>
        <a-col :span="4"><a-input :ref="ruleDetail.id+'input'" v-model="ruleDetail.inputData1" @input="change()"/></a-col>
        <a-col :span="3">
          <a-checkbox :defaultChecked="ruleDetail.detail.parame1Ignore=='Y'" :ref="ruleDetail.id+'check'" v-model="checked[ruleDetail.id][0]">
            {{ $t('vee.rule_detail2') }}
          </a-checkbox>
        </a-col>
      </a-row>
      <a-row :gutter="16" style="height:30px;line-height:30px;text-align: center;margin-top:2px;" v-if="ruleDetail.detail.parameCount>=2">
        <a-col :span="2">2</a-col>
        <a-col :span="4">{{ $t('vee.'+ruleDetail.detail.parame2Lang) }}</a-col>
        <a-col :span="4"><a-input :ref="ruleDetail.id+'input'" v-model="ruleDetail.inputData2"/></a-col>
        <a-col :span="3">
          <a-checkbox :defaultChecked="ruleDetail.detail.parame2Ignore=='Y'" :ref="ruleDetail.id+'check'" v-model="checked[ruleDetail.id][1]">
            {{ $t('vee.rule_detail2') }}
          </a-checkbox>
        </a-col>
      </a-row>
      <a-row :gutter="16" style="height:30px;line-height:30px;text-align: center;margin-top:2px;">
        <a-col :span="2">{{ ruleDetail.detail.parameCount+1 }}</a-col>
        <a-col :span="4">{{ $t('vee.rule_detail0') }}</a-col>
        <a-col :span="4">
          <a-select default-value="Y" :ref="ruleDetail.id+'pass'" v-model="selected[ruleDetail.id][0]">
            <a-select-option value="Y">
              {{ $t('common.yes') }}
            </a-select-option>
            <a-select-option value="N">
              {{ $t('common.no') }}
            </a-select-option>
          </a-select>
        </a-col>
      </a-row>
      <a-row :gutter="16" style="height:30px;line-height:30px;text-align: center;margin-top:2px;">
        <a-col :span="2">{{ ruleDetail.detail.parameCount+2 }}</a-col>
        <a-col :span="4">{{ $t('vee.rule_detail1') }}</a-col>
        <a-col :span="4">
          <a-select default-value="Y" :ref="ruleDetail.id+'alarm'" v-model="selected[ruleDetail.id][1]">
            <a-select-option value="Y">
              {{ $t('common.yes') }}
            </a-select-option>
            <a-select-option value="N">
              {{ $t('common.no') }}
            </a-select-option>
          </a-select>
        </a-col>
      </a-row>
    </div>
  </div>
</template>

<script>
import { axios } from '@/utils/request'
  export default {
    components: {
     
    },
    data() {
      return {
        data: [],
        queryParam: {},
        params:'',
        visible:false,
        callback: null,
        ruleChecked:'100',
        checkedDetail:[],
        selected:{R001:['Y','Y'],R002:['Y','Y'],R003:['Y','Y'],R004:['Y','Y'],R005:['Y','Y'],R006:['Y','Y'],R007:['Y','Y'],R008:['Y','Y'],R009:['Y','Y'],R010:['Y','Y'],R011:['Y','Y'],
        R012:['Y','Y'],R013:['Y','Y'],R014:['Y','Y'],R015:['Y','Y'],R016:['Y','Y'],R017:['Y','Y']},
        checked:{R001:[false,false],R002:[false,false],R003:[false,false],R004:[false,false],R005:[false,false],R006:[false,false],R007:[false,false],R008:[false,false],R009:[false,false],R010:[false,false],R011:[false,false],
        R012:[false,false],R013:[false,false],R014:[false,false],R015:[false,false],R016:[false,false],R017:[false,false]}
      }
    },
    mounted(){
    },
    methods: {
      change(){this.$forceUpdate()},
      showList(type,ddata){
        console.log(ddata)
        const that=this
        that.params=type
        that.visible=true
        that.ruleChecked='100'
        that.checkedDetail=[]
        that.selected={R001:['Y','Y'],R002:['Y','Y'],R003:['Y','Y'],R004:['Y','Y'],R005:['Y','Y'],R006:['Y','Y'],R007:['Y','Y'],R008:['Y','Y'],R009:['Y','Y'],R010:['Y','Y'],R011:['Y','Y'],
        R012:['Y','Y'],R013:['Y','Y'],R014:['Y','Y'],R015:['Y','Y'],R016:['Y','Y'],R017:['Y','Y']}
        that.checked={R001:[false,false],R002:[false,false],R003:[false,false],R004:[true,true],R005:[false,false],R006:[false,false],R007:[false,false],R008:[false,false],R009:[false,false],R010:[false,false],R011:[false,false],
        R012:[false,false],R013:[false,false],R014:[false,false],R015:[false,false],R016:[false,false],R017:[false,false]}
        that.initData(function () {
          ddata.forEach(row => {
            for(var j=0;j<that.data.length;j++){
              if(that.data[j].id==row.id){
                
               // that.data[j].inputData1=row.inputData1
               // that.data[j].inputData2=row.inputData2
                that.$set(that.data[j],'inputData1',row.params?row.params[0].value:'')
                that.$set(that.data[j],'inputData2',row.params?(row.params[1]?row.params[1].value:''):'')
              }
            }
            that.checkedDetail.push(row.id)
            that.selected[row.id][0]=row.pass
            that.selected[row.id][1]=row.alarm_flag
            if(row.params){
              for(var i=0;i<row.params.length;i++){
                that.checked[row.id][i]=row.params[i].ignore=='Y'?true:false
              } 
            } 
          })
        })
        
      },
      showDetail(item){
        this.ruleChecked=item.id
      //  this.ruleDetail=this.data[index].detail
      },
      initData(callback){    
        const that = this   
        return axios({
          method: 'get',
          url: '/vee/config/search-list-vrule',
          params: {type: that.params}
        }).then(res => {
          that.data=res.result
          callback()
        })
      },
      onChange(checkedValues){
       this.checkedDetail=checkedValues
      },
      getData(){
         var V_ruleindex=0
         var valiDetail = []
         this.checkedDetail.forEach(item => {
           var param=null
           if(this.$refs[item+'input']){
              for(var i=0;i<this.$refs[item+'input'].length;i++){
                if(!param){
                  param=[]
                }
                  var val={value:this.$refs[item+'input'][i].stateValue,ignore:this.checked[item][i]?'Y':'N'}
                  param.push(val)
                 // this.$refs[item+'input'][i].stateValue=''
              }
           }
           var valiobj = {
                'id':item,
                'order':V_ruleindex,
                'params':param,
                'pass':this.selected[item][0],
                'alarm_flag':this.selected[item][1],
            }
                valiDetail.push(valiobj)
                V_ruleindex++
         })
         return valiDetail
      }
     
    
    },
  }
</script>
<style lang="scss" scoped>
.selected{
    background: #ddd;
  }
  .item{
    
    padding: 5px 10px;
  
  }
  .cspan{
    display: inline-block;
  
  }
</style>