<template>
  <a-modal 
    :maskClosable="false" 
    :title="$t('common.detail')" 
    :width="1000" 
    v-model="isShow"
    @ok="onSubmit"
    @cancel="cancel" 
    :destroyOnClose="true">
    <template slot="footer">
      <a-button key="back" @click="cancel" >{{ this.$t('common.btn_cancel') }}</a-button>
    </template>
    <div class="table-page-search-wrapper">
      <a-form layout="inline">
        <a-row :gutter="20">
          <a-col :md="9" :sm="24">
            <a-form-item :label="this.$t('vee.datatype')">
              <a-select v-model="queryParam.dataType" style="width: 90%">
                <a-select-option value="05"> {{ $t('vee.ftype05') }} </a-select-option>
                <a-select-option value="06"> {{ $t('vee.ftype06') }} </a-select-option>
                <a-select-option value="07"> {{ $t('vee.ftype07') }} </a-select-option>
                <a-select-option value="09"> {{ $t('vee.ftype09') }} </a-select-option>
                <a-select-option value="10"> {{ $t('vee.ftype10') }} </a-select-option>
              </a-select>
            </a-form-item>
          </a-col>
          <a-col :md="9" :sm="24">
            <a-form-item :label="this.$t('vee.obis')">
              <a-input v-model="queryParam.id" style="width: 90%" @search="onSearch"/>
            </a-form-item>
          </a-col>
          <a-col :md="4" :sm="48">
            <span class="table-page-search-submitButtons">
              <btn-search @click="onSearch" :showInput="false"></btn-search>
            </span>
          </a-col>
        </a-row>
      </a-form>
      <s-table  
        ref="table"
        :bordered="false"
        :autoLoad="true"
        :data="loadData"
        size="small"
        rowKey="id"
      >
        <a-table-column 
          :title="this.$t('task.index1')"
          width="50px"
          align="center"
        >
          <span slot-scope="text">
            <a-icon type="schedule" theme="twoTone" class="icon" style="font-size:25px"/>
          </span>
        </a-table-column>
        <a-table-column
          :title="this.$t('vee.datatype')"
          width="150px"
          dataIndex="dataType"
        >
          <span slot-scope="text,record">
            {{ $t('vee.ftype'+record.dataType) }}
          </span>
        </a-table-column>
        <a-table-column
          :title="this.$t('vee.f_index1')"
          width="150px"
          dataIndex="shortCode"
        ></a-table-column>
        <a-table-column
          :title="this.$t('vee.obis')"
          width="150px"
          dataIndex="id"
        >
        </a-table-column>
        <a-table-column
          :title="this.$t('vee.f_index2')"
          width="250px"
          dataIndex="description"
        >
        </a-table-column>
      </s-table>
    </div>
  </a-modal>
</template>

<script>
  import STable from '@/components/table/'
  import Dict from '@/views/public/Dict'
  import {
    axios
  } from '@/utils/request'
  export default {
    components: {
      STable,
      Dict, 
    },
    data() {
      return {
        data: {},
        isShow: false,
        queryParam: {dataType:'05'},
        fieldsData:{dataitem05:[],dataitem06:[],dataitem07:[],dataitem09:[],dataitem10:[]},
        loadData: (parameter) => {    
          this.queryParam.code=this.params.code
          return axios({
            method: 'get',
            url: '/vee/config/search-fields-detail',
            params: Object.assign(parameter, this.queryParam)
          }).then(res => {
            return res.result
          })
        }
      }
    },
    methods: {
      showModal(params) {
        this.isShow = true
        this.params=params
      },
      onSearch() {
        this.$refs.table.refresh()
      },
      onSubmit () {        
        this.isShow = false
      },
      cancel() {
        this.isShow = false
      },
    },
  }
</script>
