<template>
  <div style="background-color: #fff;padding:10px 20px">
    <div :spinning="loading">
      <a-form :form="form" style="margin-top: 0px">
        <a-row :gutter="16">
          <a-col :xl="{span:7}" :lg="{span:7}" :md="12" :sm="24">
            <a-form-item :label="$t('vee.index0')" v-bind="formItemLayout">
              <a-input 
                :disabled="true"
                v-decorator="[
                  'code', 
                  {initialValue: data.code}
                ]"
              />
            </a-form-item>
          </a-col>
          <a-col :xl="{span:7}" :lg="{span:7}" :md="12" :sm="24">
            <a-form-item :label="$t('vee.index1')" v-bind="formItemLayout">
              <a-input 
                v-decorator="[
                  'description', 
                  {initialValue: data.description, rules: [{required: true,message:$t('vee.index1')+' '+$t('common.notnull')}] }
                ]"
              />
            </a-form-item>
          </a-col>
        </a-row>
        <a-row :gutter="16" style="margin-top:20px;height:10px;background:#f0f2f5">
        </a-row>
        <a-row :gutter="16" style="height:30px">
          <span style="display:inline-block;padding:10px 10px;font-size:15px;color:#1890FF">{{ $t('vee.title1') }} :</span>
        </a-row>
        <a-row :gutter="16">
          <a-col :xl="{span:7}" :lg="{span:7}" :md="12" :sm="24">
            <a-form-item :label="this.$t('dept.index')" v-bind="formItemLayout">
              <dept-select 
                @change="getId"
                v-decorator="[
                  'deptId',
                  {initialValue: data.deptId?data.deptId:''}
                ]" />
            </a-form-item>
          </a-col>
          <a-col :xl="{span:7}" :lg="{span:7}" :md="12" :sm="24">
            <a-form-item :label="this.$t('device.index4')" v-bind="formItemLayout">
              <dict 
                @change="getSort"
                mode="multiple"
                type="METER_SORT"
                table="params"
                v-decorator="[
                  'meterSort',
                  { initialValue: data.meterSort?data.meterSort.split('|'):[]}
                ]"
              ></dict>
            </a-form-item>
          </a-col>
          <a-col :xl="{span:7}" :lg="{span:7}" :md="12" :sm="24">
            <a-form-item :label="this.$t('device.index1')" v-bind="formItemLayout">
              <a-input-group compact>
                <a-input 
                  style="width:80%" 
                  :disabled="true" 
                  v-decorator="[
                    'deviceId',
                    {initialValue: data.deviceId, rules: [{required: true,message:$t('device.index1')+' '+$t('common.notnull')}] }
                  ]" />
                <span style="width:20%">
                  <a-button @click="queryProd" icon="search" style="width:100%"></a-button>
                </span>
              </a-input-group>
            </a-form-item>
          </a-col>
        </a-row>
        <a-row :gutter="16" style="margin-top:20px;height:10px;background:#f0f2f5">
        </a-row>
        <a-row :gutter="16" style="height:30px">
          <span style="display:inline-block;padding:10px 10px;font-size:15px;color:#1890FF">{{ $t('vee.title2') }} :</span>
        </a-row>
        <a-checkbox-group @change="onChange" style="display:block" :value="checkedList">
          <a-row :gutter="16">
            <a-form-item :label="this.$t('vee.files') " v-bind="formItemLayout" :label-col="{span:3}" :wrapper-col="{span:20}">
              <a-col :span="7" :offset="1" :class="{'selected':vType=='05'}">
                <a-checkbox value="05" @click="showRule('05')" >
                </a-checkbox>
                <span @click="showRule('05')">
                  <span> {{ $t('vee.ftype05') }} </span>&nbsp;&nbsp;&nbsp;
                  <span> {{ $t('vee.filescount') }} <span>{{ filesCount.t05 }}</span></span>&nbsp;&nbsp;&nbsp;
                  <a-button @click="searchFiles('05')">{{ $t('common.select') }}</a-button>
                </span>
              </a-col>
              <a-col :span="7" :class="{'selected':vType=='06'}">
                <a-checkbox value="06" @click="showRule('06')">
                </a-checkbox>
                <span @click="showRule('06')">
                  <span> {{ $t('vee.ftype06') }} </span>&nbsp;&nbsp;&nbsp;
                  <span> {{ $t('vee.filescount') }} <span>{{ filesCount.t06 }}</span></span>&nbsp;&nbsp;&nbsp;
                  <a-button @click="searchFiles('06')">{{ $t('common.select') }}</a-button>
                </span>
              </a-col>
              <a-col :span="7" :class="{'selected':vType=='07'}">
                <a-checkbox value="07" @click="showRule('07')">
                </a-checkbox>
                <span @click="showRule('07')">
                  <span> {{ $t('vee.ftype07') }} </span>&nbsp;&nbsp;&nbsp;
                  <span> {{ $t('vee.filescount') }} <span>{{ filesCount.t07 }}</span></span>&nbsp;&nbsp;&nbsp;
                  <a-button @click="searchFiles('07')">{{ $t('common.select') }}</a-button>
                </span>
              </a-col>
            </a-form-item>
          </a-row> 
          <a-row :gutter="16">
            <a-form-item :label="this.$t('vee.kwh') " v-bind="formItemLayout" :label-col="{span:3}" :wrapper-col="{span:20}">
              <a-col :span="7" :offset="1" :class="{'selected':vType=='09'}">
                <a-checkbox value="09" @click="showRule('09')" >
                </a-checkbox>
                <span @click="showRule('09')">
                  <span> {{ $t('vee.ftype09') }} </span>&nbsp;&nbsp;&nbsp;
                </span>
              </a-col>
              <a-col :span="7" :class="{'selected':vType=='10'}">
                <a-checkbox value="10" @click="showRule('10')">
                </a-checkbox>
                <span @click="showRule('10')">
                  <span> {{ $t('vee.ftype10') }} </span>
                </span>
              </a-col>
            </a-form-item>
          </a-row>
        </a-checkbox-group> 
        <a-row :gutter="16" style="margin-top:20px;height:10px;background:#f0f2f5">
        </a-row>
        <a-tabs default-active-key="1" @change="callback" :activeKey="ruleType">
          <a-tab-pane key="1" :tab="this.$t('vee.rule1')">
            <rule-list :params="vType" ref="rulelist"></rule-list>
          </a-tab-pane>
          <a-tab-pane key="2" :tab="this.$t('vee.rule2')" :disabled="vType=='07'||vType=='06'">
            <rule-liste :params="vType" ref="ruleliste"></rule-liste>
          </a-tab-pane>
        </a-tabs>
      </a-form>
    </div> 
    <search-meter ref="selectMeter" @ok="loadDeviceData" :params="meterParam"></search-meter>
    <search-files ref="selectFiles" @ok="loadFilesData"></search-files>
    <footer-tool-bar :style="{ width: '100%'}">
      <a-button @click="onCancel">{{ $t('common.btn_cancel') }}</a-button>&nbsp;&nbsp;
      <a-button @click="onSubmit" type="primary" v-if="params.opType!='3'">{{ $t('common.btn_ok') }}</a-button>
    </footer-tool-bar>
  </div>
</template>
<script>
  import { axios } from '@/utils/request'
  import Dict from '@/views/public/Dict'
  import DeptSelect from '@/views/public/DeptSelect'
  import RuleList from '@/views/vee/config/RuleList'
  import RuleListe from '@/views/vee/config/RuleListe'
  import SearchMeter from '@/views/public/SearchMeter'
  import SearchFiles from '@/views/public/SearchFiles'
  import FooterToolBar from '@/components/FooterToolbar'
  import notification from 'ant-design-vue/es/notification'

  export default {
    components: {
      Dict,
      FooterToolBar,
      notification,
      SearchMeter,
      DeptSelect,
      RuleList,
      RuleListe,
      SearchFiles
    },
    data() {
      return {
        loading: false,
        form: this.$form.createForm(this),
        data: {deviceId:'',fieldsCount:0,rulesCount:0},
        filesCount:{t05:0,t06:0,t07:0},
        meterParam:{},
        confirmLoading: false,
        vType:'',
        ruleType:'1',
        checkedList:[],
        ruleData:{dataitem05:[],ruletype05:[],Eruletype05:[],
        dataitem06:[],ruletype06:[],
        dataitem07:[],ruletype07:[],
        ruletype09:[],Eruletype09:[],
        ruletype10:[],Eruletype10:[]},
        formItemLayout: {labelCol: {lg: {span: 7}, sm: {span: 7}},wrapperCol: {lg: {span: 14}, sm: {span: 14}}}
      }
    },
    props: {
      params: {
        type: Object,
        default: null
      }
    },
    mounted(){
      this.initModel()
    },
    
    methods: {
      searchFiles(type){
        this.$refs.selectFiles.showModal(type,this.ruleData['dataitem'+type])
      },
      loadFilesData(items) {
        this.ruleData['dataitem'+this.vType]=[]
        items.forEach(item =>{
          this.ruleData['dataitem'+this.vType].push({id:item})
        })
        this.filesCount['t'+this.vType]=items.length
      },
      showRule(type){
        if(this.vType==type){
           return false
        }
        if(type=='06' || type=='07'){
          if(this.ruleType=='2'){
            this.ruleData['Eruletype'+this.vType]=this.$refs.ruleliste.getData()
          }else{
             this.ruleData['ruletype'+this.vType]=this.$refs.rulelist.getData()
          }
         this.ruleType='1'
         this.vType=type
         this.$refs.rulelist.showList(this.vType,this.ruleData['ruletype'+this.vType])
        }else{
          if(this.ruleType=='1'){
            if(this.vType!=type){
              this.ruleData['ruletype'+this.vType]=this.$refs.rulelist.getData()
              this.vType=type
              this.$refs.rulelist.showList(this.vType,this.ruleData['ruletype'+this.vType])
            }
          }else{
            if(this.vType!=type){
              this.ruleData['Eruletype'+this.vType]=this.$refs.ruleliste.getData()
              this.vType=type
              this.$refs.ruleliste.showList(this.vType,this.ruleData['Eruletype'+this.vType])
            }
          }
        }
        
      },
      onChange(checkedValues){
        console.log(checkedValues)
        this.checkedList=checkedValues
      },
      callback(key){
         this.ruleType=key
        if(this.vType!=''){
          if(key==1){
            this.ruleData['Eruletype'+this.vType]=this.$refs.ruleliste.getData()
            this.$refs.rulelist.showList(this.vType,this.ruleData['ruletype'+this.vType])
            
          }else{
            setTimeout(() => {
              this.$refs.ruleliste.showList(this.vType,this.ruleData['Eruletype'+this.vType])
            }, 100)
            this.ruleData['ruletype'+this.vType]=this.$refs.rulelist.getData()
          }
        }
      },
      getId(values){
        this.meterParam.deptId=values
      },
       getSort(values){
        this.meterParam.meterSort=values
      },
      queryProd() {
        this.data.deviceId=''
        this.$refs.selectMeter.showModal()
      },
      loadDeviceData(items) {console.log(items)
        items.forEach(item =>{
          this.data.deviceId+=item.deviceNum+','
        })
        this.data.deviceId=this.data.deviceId.substr(0,this.data.deviceId.length -1)
        this.form.setFieldsValue({'deviceId': this.data.deviceId})
      },
      initModel(){
        if(this.params.opType=='2'){
          this.initData('/vee/config/update-load')
        }else if(this.params.opType=='3'){
          this.initData('/vee/config/detail')
        }else{
           this.getCode()
        }
      },
      initData(url){
        const that = this
        that.loading = true
        axios({
          url:url,
          method: 'get',
          params: {code: that.params.code}
        }).then(response=>{
          that.loading = false
          that.data = response.result
          this.initRuledata()
        })
      },
      initRuledata(){
         this.data.dataTypeDetails=JSON.parse(this.data.dataTypeDetails)
         this.data.dataTypeDetails.forEach(row => {
            row.validation_rules=row.validation_rules?row.validation_rules:[]
            row.estimate_rules=row.estimate_rules?row.estimate_rules:[]
            if(row.data_type=='05'){
                this.ruleData.dataitem05=row.fieldids?row.fieldids:[]
                this.ruleData.ruletype05=row.validation_rules
                this.ruleData.Eruletype05=row.estimate_rules
                this.filesCount.t05=row.fieldids?row.fieldids.length:0
            }else if(row.data_type=='06'){
                this.ruleData.dataitem06=row.fieldids?row.fieldids:[]
                this.ruleData.ruletype06=row.validation_rules
                 this.filesCount.t06=row.fieldids?row.fieldids.length:0
            }else if(row.data_type=='07'){
                this.ruleData.dataitem07=row.fieldids?row.fieldids:[]
                this.ruleData.ruletype07=row.validation_rules
                 this.filesCount.t07=row.fieldids?row.fieldids.length:0
            }else if(row.data_type=='09'){
                this.ruleData.ruletype09=row.validation_rules
                this.ruleData.Eruletype09=row.estimate_rules
            }else if(row.data_type=='10'){
                this.ruleData.ruletype10=row.validation_rules
                this.ruleData.Eruletype10=row.estimate_rules
            }
            this.checkedList.push(row.data_type)
         })
      },
      getCode(){
        const that = this
        that.loading = true
        axios({
          url:'/vee/config/search-code',
          method: 'get',
          params: {}
        }).then(response=>{
          that.loading = false
          that.data.code = response.result
        })
      },
      onSubmit(){
        if(this.ruleType=='1'){
           this.ruleData['ruletype'+this.vType]=this.$refs.rulelist.getData()
        }else{
           this.ruleData['Eruletype'+this.vType]=this.$refs.ruleliste.getData()
        }
        
        this.form.validateFields((err,values) => {
          if (!err) {            
            const that = this
            that.data = Object.assign(that.data,values)
            if(!that.params){
              return
            }
            that.data.dataTypeDetails=this.getruleData()
            that.data.dataTypeDetails=JSON.stringify(that.data.dataTypeDetails)
            that.data.meterSort=that.data.meterSort?that.data.meterSort.join('|'):''
            console.log(that.data)
            that.loading = true
            axios({
              url: that.params.opType=='1'?'/vee/config/insert':'/vee/config/update',
              method: 'post',
              data: that.data
            }).then(result=>{
              that.loading = false
              if(result){
                this.$emit('ok',result)
              }
            })
          }
        })
      },
      getruleData(){
        this.data.fieldsCount=0
        this.data.rulesCount=0
        var big_arr=[]
        this.checkedList.forEach(item => {
          var big_obj={}
          big_obj.data_type=item
          big_obj.valid='Y'
          if(item=='05'){
             big_obj.fieldids=this.ruleData.dataitem05
             big_obj.validation_rules=this.ruleData.ruletype05
             big_obj.estimate_rules=this.ruleData.Eruletype05
          }else if(item=='06'){
              big_obj.fieldids=this.ruleData.dataitem06
              big_obj.validation_rules=this.ruleData.ruletype06
          }else if(item=='07'){
              big_obj.fieldids=this.ruleData.dataitem07
              big_obj.validation_rules=this.ruleData.ruletype07
          }else if(item=='09'){
              big_obj.validation_rules=this.ruleData.ruletype09
              big_obj.estimate_rules=this.ruleData.Eruletype09
              if(this.ruleData.Eruletype09.length>0 ||  this.ruleData.ruletype09.length>0){
                big_obj.fieldids=[{'id':'VEE30012'},{'id':'VEE50012'},{'id':'VEE00012'},{'id':'VEE40012'},{'id':'VEE60012'}]
              }
          }else if(item=='10'){
              big_obj.validation_rules=this.ruleData.ruletype10
              big_obj.estimate_rules=this.ruleData.Eruletype10
              if(this.ruleData.Eruletype10.length>0 ||  this.ruleData.ruletype10.length>0){
                big_obj.fieldids=[{'id':'VEE30112'},{'id':'VEE50112'},{'id':'VEE00112'},{'id':'VEE40112'},{'id':'VEE60112'}]
              }
          }
          big_arr.push(big_obj)
          this.data.fieldsCount+=big_obj.fieldids?big_obj.fieldids.length:0
          this.data.rulesCount+=big_obj.validation_rules?big_obj.validation_rules.length:0
          this.data.rulesCount+=big_obj.estimate_rules?big_obj.estimate_rules.length:0
        })
        console.log(big_arr)
        return big_arr
      },
      onCancel(){
        this.$emit('cancel')
      },
    }
  }
</script>
<style lang="scss" scoped>
  .card{
    margin-bottom: 24px
  }
  .ant-row{
    height: 35px
  }
  .selected{
    background: #ddd
  }
</style>
