<template>
  <a-modal 
    :maskClosable="false" 
    :title="$t('common.detail')" 
    :width="1000" 
    v-model="isShow"
    @ok="onSubmit"
    @cancel="cancel" 
    :destroyOnClose="true">
    <template slot="footer">
      <a-button key="back" @click="cancel" >{{ this.$t('common.btn_cancel') }}</a-button>
    </template>
    <div class="table-page-search-wrapper">
      <a-form layout="inline">
        <a-row :gutter="20">
          <a-col :md="9" :sm="24">
            <a-form-item :label="this.$t('vee.datatype')">
              <a-select v-model="queryParam.dataType" style="width: 90%">
                <a-select-option value="05"> {{ $t('vee.ftype05') }} </a-select-option>
                <a-select-option value="06"> {{ $t('vee.ftype06') }} </a-select-option>
                <a-select-option value="07"> {{ $t('vee.ftype07') }} </a-select-option>
                <a-select-option value="09"> {{ $t('vee.ftype09') }} </a-select-option>
                <a-select-option value="10"> {{ $t('vee.ftype10') }} </a-select-option>
              </a-select>
            </a-form-item>
          </a-col>
          <a-col :md="9" :sm="24">
            <a-form-item :label="this.$t('vee.rule1')">
              <a-input v-model="queryParam.name" style="width: 90%" />
            </a-form-item>
          </a-col>
          <a-col :md="4" :sm="48">
            <span class="table-page-search-submitButtons">
              <btn-search @click="onSearch" :showInput="false"></btn-search>
            </span>
          </a-col>
        </a-row>
      </a-form>
      <a-row :gutter="16" style="height:2px;background:#f0f2f5">
      </a-row>
      <a-tabs default-active-key="1" @change="callback" :activeKey="ruleType">
        <a-tab-pane key="1" :tab="this.$t('vee.rule1')">
          <s-table  
            ref="table"
            :bordered="false"
            :autoLoad="true"
            :data="loadData"
            size="small"
            rowKey="id"
          >
            <a-table-column 
              :title="this.$t('task.index1')"
              width="50px"
              align="center"
            >
              <span slot-scope="text">
                <a-icon type="schedule" theme="twoTone" class="icon" style="font-size:25px"/>
              </span>
            </a-table-column>
            <a-table-column
              :title="this.$t('vee.datatype')"
              width="150px"
            >
              <span slot-scope="tetx,record">
                {{ $t('vee.ftype'+record.dataType) }}
              </span>
            </a-table-column>
            <a-table-column
              :title="this.$t('vee.rule1')"
              width="150px"
              dataIndex="nameLang"
            >
              <span slot-scope="tetx,record">
                {{ $t('vee.'+record.id+'_L') }}
              </span>
            </a-table-column>
            <a-table-column
              :title="this.$t('vee.rule_detail0')"
              width="150px"
              dataIndex="id"
            >
              <span slot-scope="text,record">
                <span v-if="record.pass=='Y'">{{ $t('common.yes') }}</span>
                <span v-if="record.pass=='N'">{{ $t('common.no') }}</span>
              </span>
            </a-table-column>
            <a-table-column
              :title="this.$t('vee.rule_detail1')"
              width="150px"
            >
              <span slot-scope="text,record">
                <span v-if="record.alarmFlag=='Y'">{{ $t('common.yes') }}</span>
                <span v-if="record.alarmFlag=='N'">{{ $t('common.no') }}</span>
              </span>
            </a-table-column>
          </s-table>
        </a-tab-pane>
        <a-tab-pane key="2" :tab="this.$t('vee.rule2')">
          <s-table  
            ref="table1"
            :bordered="false"
            :autoLoad="true"
            :data="loadData"
            size="small"
            rowKey="id"
          >
            <a-table-column 
              :title="this.$t('task.index1')"
              width="50px"
              align="center"
            >
              <span slot-scope="text">
                <a-icon type="schedule" theme="twoTone" class="icon" style="font-size:25px"/>
              </span>
            </a-table-column>
            <a-table-column
              :title="this.$t('vee.datatype')"
              width="150px"
            >
              <span slot-scope="tetx,record">
                {{ $t('vee.ftype'+record.dataType) }}
              </span>
            </a-table-column>
            <a-table-column
              :title="this.$t('vee.rule1')"
              width="150px"
              dataIndex="nameLang"
            >
              <span slot-scope="tetx,record">
                {{ $t('vee.'+record.id+'_L') }}
              </span>
            </a-table-column>
            <a-table-column
              :title="this.$t('vee.value')"
              width="150px"
              dataIndex="params"
            >
            </a-table-column>
          </s-table>
        </a-tab-pane>
      </a-tabs>
    </div>
  </a-modal>
</template>

<script>
  import STable from '@/components/table/'
  import Dict from '@/views/public/Dict'
  import {
    axios
  } from '@/utils/request'
  export default {
    components: {
      STable,
      Dict, 
    },
    data() {
      return {
        data: {},
        isShow: false,
        ruleType:'1',
        queryParam: {dataType:'05'},
        loadData: (parameter) => {    
          const that = this    
          parameter.code =that.params 
          parameter.dataType =that.queryParam.dataType 
          parameter.ruleType =that.ruleType
          return axios({
            method: 'get',
            url: '/vee/config/search-rules-detail',
            params: Object.assign(parameter, this.queryParam)
          }).then(res => {
            return res.result
          })
        }
      }
    },
    methods: {
      callback(key){
        this.ruleType=key
        if(this.ruleType==1){
           this.$refs.table.refresh()
        }else{
          this.$refs.table1.refresh()
        }
      },
      showModal(params) {
        this.isShow = true
        this.params=params
      
      },
      onSearch() {
        if(this.ruleType==1){
           this.$refs.table.refresh()
        }else{
          this.$refs.table1.refresh()
        }
       
        
      },
      onSubmit () {        
        this.isShow = false
      },
      cancel() {
        this.isShow = false
      },
    },
  }
</script>
