<template>
  <a-modal 
    :maskClosable="false" 
    :title="$t('vee.title2')" 
    :width="1000" 
    v-model="isShow"
    @ok="onSubmit"
    @cancel="cancel" 
    :destroyOnClose="true">
    <div class="table-page-search-wrapper">
      <a-form layout="inline">
        <a-row :gutter="20">
          <a-col :md="9" :sm="24">
            <a-form-item :label="this.$t('vee.obis')">
              <a-input v-model="queryParam.id" style="width: 90%" @search="onSearch"/>
            </a-form-item>
          </a-col>
          <a-col :md="9" :sm="24">
            <a-form-item :label="this.$t('vee.f_index2')">
              <a-input v-model="queryParam.description" style="width: 90%" @search="onSearch"/>
            </a-form-item>
          </a-col>
          <a-col :md="4" :sm="48">
            <span class="table-page-search-submitButtons">
              <btn-search @click="onSearch" :showInput="false"></btn-search>
            </span>
          </a-col>
        </a-row>
      </a-form>
      <s-table  
        ref="table"
        :bordered="false"
        :autoLoad="true"
        :data="loadData"
        size="small"
        rowKey="id"
        :rowSelection="{type: multiple ? 'checkbox' : 'radio', fixed: false, onChange: onSelect,selectedRowKeys:selected,onSelect:onCan,onSelectAll:onSelectAll}"
      >
        <a-table-column
          :title="this.$t('vee.f_index1')"
          width="150px"
          dataIndex="shortCode"
        >
        </a-table-column>
        <a-table-column
          :title="this.$t('vee.obis')"
          width="150px"
          dataIndex="id"
        ></a-table-column>
        <a-table-column
          :title="this.$t('vee.f_index2')"
          width="250px"
          dataIndex="description"
        >
        </a-table-column>
        <a-table-column
          :title="this.$t('vee.f_index3')"
          width="100px"
          dataIndex="dataUnit"
        >
        </a-table-column>
      </s-table>
    </div>
  </a-modal>
</template>

<script>
  import STable from '@/components/table/'
  import Dict from '@/views/public/Dict'
  import { isFunction } from 'util'
  import {
    axios
  } from '@/utils/request'
  export default {
    components: {
      STable,
      Dict,
    },
    props: {
      multiple: {
        type: Boolean,
        default: true
      },
    },
    data() {
      return {
        selectedRows: [],
        data: {},
        isShow: false,
        queryParam: {},
        cashData: {},
        selectRows: [],
        autoLoad: true,
        isAdmin: false,
        isEnding:false,
        callback: null,
        selected:[],
        loadData: (parameter) => {  
          const that = this    
          parameter.type=  that.params   
          return axios({
            method: 'get',
            url: '/vee/config/search-list-fields',
            params: Object.assign(parameter, this.queryParam)
          }).then(res => {
            return res.result
          })
        }
      }
    },
    methods: {
      showModal(params,selectedFiles ,callback) {
        
        this.isShow = true
        this.queryParam={}
        this.selected=[]
        selectedFiles.forEach(item => {
          this.selected.push(item.id)
        })
        
        this.selectedRowKeys = this.selectedRows = []
        this.isAdmin = params.isAdmin?params.isAdmin:false
        this.isEnding=params.isEnding?params.isEnding:false
        this.params=params
        this.callback = callback
      },
      onSearch() {
        this.$refs.table.refresh()
      },
      onSubmit () {        
        if(this.selected.length) {
          this.$emit('ok',this.selected,this.searchWord)
        }else{
          return false
        }
        if(isFunction(this.callback)){
          this.callback(this.selected)
          this.visible = false
        }
        this.isShow = false
      },
      cancel() {
        this.isShow = false
      },
      onSelect(selectedRowKeys, selectedRows) {
        selectedRows.forEach(item =>{
          if(this.selected.indexOf(item.id)<0)
          this.selected.push(item.id)
        })
        this.selectedRows = selectedRows
      },
      onCan(record, selected){
        if(!selected){
          this.selected.splice(this.selected.indexOf(record.id),1)
        }
      },
      onSelectAll(selected){
        if(!selected){
          this.selected=[]
        }
      }
    },
  }
</script>
